 @use "./fonts.scss";

// ============= VARIABLES ================

// FONTS
@mixin font-display {
  font-family: "Uertas";
  font-size: 200px;
}

@mixin font-h2 {
  font-family: "BDO Grotesk";
  font-size: 84px;
  font-weight: 100;
  text-transform: uppercase;
}

@mixin font-h3 {
  font-family: "BDO Grotesk";
  font-size: 69px;
  font-weight: 900;
}

@mixin font-h4 {
  font-family: "BDO Grotesk";
  font-size: 48px;
  font-weight: 900;
  text-transform: uppercase;
}

@mixin font-decoration {
  font-family: "Coupeur Jack";
  font-size: 36px;
}

@mixin font-body {
  font-family: "Consolas";
  font-size: 30px;
}

// COLORS
$color: (
  "light": #fcf5da,
  "dark": #000,
  "transparent": rgba(#000, 0.5),
);

// SIZES
$sizes: (
  "margin-1": 30px,
  "margin-2": 100px,
  "border":
    map-get(
      $map: $color,
      $key: "light",
    )
    solid 2px,
);
