@use "./global" as *;

.navbar {
  position: fixed;
  right: map-get($map: $sizes, $key: "margin-1");
  top: map-get($map: $sizes, $key: "margin-2");

  height: calc(100vh - 2 * map-get($sizes, "margin-2"));
  background-color: map-get($map: $color, $key: "light");

  border-left: map-get($map: $sizes, $key: "border");
}