@use "./global.scss" as *;

.front {

  flex-grow: 2;
  flex-basis: auto;
  height: 100vh;
  align-items: center;

  .text {

    * {
      width: fit-content;
    }

    h5 {
      position: relative;
      top: -60px;
      border-top: map-get($map: $sizes, $key: "border");
    }
  }
}