.intro-text {
  border-left: 2px solid black;
}

.contact {
  height: 100vh;
  justify-content: center;
  align-items: center;
  position: relative;
}
.contact .text {
  text-align: center;
  width: fit-content;
}

.header {
  position: fixed;
  display: flex;
  flex-direction: row;
  left: 20px;
  top: 20px;
  border: 2px solid black;
}
.header a img {
  height: 32px;
  width: 32px;
  margin: 10px 15px;
}

